<template>
  <div class="product__inner">
    <div v-if="product" class="product__filter">
      <div
        class="product__filter__item"
        :class="{'show' : isOpen === 'Модель'}"
      >
        <h3>Модель
          <span class="delete"
            @click="deleteModels"
            v-if="checkedModels.length > 0">&times;</span>
        </h3>
        <ul>
          <li v-for="model in models" :key="model.id">

            <p v-for="item in model.products" :key="item.id">
              <span v-if="item.name === product.name" class="link">
                <label>
                   <input
                     type="checkbox"
                     name="model"
                     :value="model.title"
                     :checked="checked"
                     v-model="checkedModels"
                     @change="changeState(model)"
                   >
                   <a>{{model.title}}</a>
                </label>
              </span>

            </p>
          </li>
        </ul>
      </div>
      <div
        class="product__filter__item"
        :class="{'show' : isOpen === 'Тип скрепления'}"
      >
        <h3>Тип скрепления
          <span class="delete"
            @click="deleteBonds"
            v-if="checkedBonds.length > 0"
          >&times;</span>
        </h3>
        <ul>
          <li v-for="bond in filteredModels" :key="bond.id">

            <p v-for="item in bond.products" :key="item.id">
              <span v-if="item.name === product.name" class="link">
                <label>
                   <input
                     type="checkbox"
                     name="bond"
                     :value="bond.title"
                     v-model="checkedBonds"
                     @change="changeState(bond)"
                   >
                   <a>{{bond.title}}</a>
                </label>

              </span>

            </p>
          </li>
        </ul>
      </div>
      <div
        class="product__filter__item"
        :class="{'show' : isOpen === 'Вид блока'}"
      >
        <h3>Вид блока
          <span class="delete"
            @click="deleteBlocks"
            v-if="checkedBlocks.length > 0"
          >&times;
          </span>
        </h3>
        <ul>
          <li v-for="block in filteredBonds" :key="block.id">

            <p v-for="item in block.products" :key="item.id">
              <span v-if="item.name === product.name" class="link">
                <label>
                   <input
                     type="checkbox"
                     name="block"
                     :value="block.title"
                     v-model="checkedBlocks"
                     @change="changeState(block)"
                   >
                   <a>{{block.title}}</a>
                </label>
              </span>


            </p>
          </li>
        </ul>
      </div>
      <div
        class="product__filter__item product__filter__formats"
        :class="{'show' : isOpen === 'Формат'}"
      >
        <h3>Формат</h3>
        <ul class="tag__inner">
          <li v-for="format in filteredBlocks" :key="format.id" class="tag">

            <p v-for="item in format.products" :key="item.id">
              <span v-if="item.name === product.name">
                <span class="tag__link">{{format.title}}</span>
              </span>

            </p>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  props: ['product', 'isOpen'],
  data() {
    return {
      checkedModels: [],
      checkedBonds: [],
      checkedBlocks: [],
      queryModels: [],
      queryBonds: [],
      queryBlocks: [],
      checked: false
    }
  },
  computed: {
    ...mapGetters([
      'models', 'bonds', 'blocks', 'formats',
      'getcheckedModels', 'getcheckedBonds', 'getcheckedBlocks',
      'getqueryModels', 'getqueryBonds', 'getqueryBlocks',
    ]),

    filteredModels: function() {
      if(this.checkedModels.length == 0) return this.bonds;

      return this.bonds.filter(bond =>
        bond.models.some(model => this.checkedModels.includes(model.name))

      )
    },
    filteredBonds: function() {
      if(this.checkedBonds.length == 0) return this.blocks;
      return this.blocks.filter(block =>
        block.bonds.some(bond => this.checkedBonds.includes(bond.name))
      )
    },
    filteredBlocks: function() {
      if(this.checkedBlocks.length == 0) return this.formats;
      return this.formats.filter(format =>
        format.blocks.some(block => this.checkedBlocks.includes(block.name))
      )
    },

  },
  methods: {
    deleteModels() {
      this.checkedModels = []
      this.queryModels = []
      this.$store.commit('checkedModels', this.checkedModels)
      this.$store.commit('queryModels', this.queryModels)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },

    deleteBonds() {
      this.checkedBonds = []
      this.queryBonds = []
      this.$store.commit('checkedBonds', this.checkedBonds)
      this.$store.commit('queryBonds', this.queryBonds)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },

    deleteBlocks() {
      this.checkedBlocks = []
      this.queryBlocks = []
      this.$store.commit('checkedBlocks', this.checkedBlocks)
      this.$store.commit('queryBlocks', this.queryBlocks)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },

    changeState(elem) {

      const elemType = event.target.getAttribute('name')

      switch(elemType) {
          case 'model':
            if(!event.target.checked)
              this.queryModels = this.deleteQuery(this.queryModels, elem.id)
            else
              this.queryModels.push(elem.id)
              this.queryModels = this.filterQuery(this.queryModels)
            break;

          case 'bond':
            if(!event.target.checked)
              this.queryBonds = this.deleteQuery(this.queryBonds, elem.id)
            else
              this.queryBonds.push(elem.id)
              this.queryBonds = this.filterQuery(this.queryBonds)
            break;

          case 'block':
            if(!event.target.checked)
              this.queryBlocks = this.deleteQuery(this.queryBlocks, elem.id)
            else
              this.queryBlocks.push(elem.id)
              this.queryBlocks = this.filterQuery(this.queryBlocks)
            break;
        }

      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
      this.commitStore()

    },

    commitStore() {
      this.$store.commit('checkedModels', this.checkedModels)
      this.$store.commit('checkedBonds', this.checkedBonds)
      this.$store.commit('checkedBlocks', this.checkedBlocks)
      this.$store.commit('queryModels', this.queryModels)
      this.$store.commit('queryBonds', this.queryBonds)
      this.$store.commit('queryBlocks', this.queryBlocks)
    },

    filterQuery(query) {
      let params = [];
      for (let i = 0; i < query.length; i++) {
        params[query[i]] = query[i];
      }
      return Object.values(params);
    },

    deleteQuery(query, index) {
      let filtered = query.filter(item => {
        return item != index
      })
      return query = filtered

    },

    routerPush(model, bond, block) {
      this.$router.push({
        query: {'model': model, 'bond': bond, 'block': block}
      })
    }
  },

  watch: {

    getcheckedModels() {
      this.checkedModels = this.getcheckedModels
    },

    getcheckedBonds() {
      this.checkedBonds = this.getcheckedBonds
    },

    getcheckedBlocks() {
      this.checkedBlocks = this.getcheckedBlocks
    },
  }

}
</script>
