import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Products from './views/Products.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass: 'active',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      redirect: '/products/ezhednevniki'
    },
    {
      path: '/products',
      component: Products,
      redirect: '/products/ezhednevniki'
    },
    {
      path: '/products/:id',
      component: Products,
      props: true
    },

  ]
})
