<template>
  <div class="products">
    <div class="container">
      <Breadcrumbs :name='product.name' />
      <div class="products__title">
        <h1>{{product.name}}</h1>

      </div>
      <div class="products__content content">
        <div class="products__selects">

          <div
            class="products__select"
            name="Изделия"
            @click="openSelect"
            :class="{'open' : openedSelect === 'Изделия'}"
            >
            Изделия
            <span class="delete">&times;</span>
          </div>

          <div
            class="products__select"
            name="Модель"
            @click="openSelect"
            :class="{'open' : openedSelect === 'Модель', 'selected': checkedModels.length > 0}"
            >
            Модель
            <span class="delete" @click="deleteModels">&times;</span>
          </div>

          <div
            class="products__select"
            name="Тип скрепления"
            @click="openSelect"
            :class="{'open' : openedSelect === 'Тип скрепления', 'selected': checkedBonds.length > 0}"
            >
            Тип скрепления
            <span class="delete" @click="deleteBonds">&times;</span>
          </div>

          <div
            class="products__select"
            name="Вид блока"
            @click="openSelect"
            :class="{'open' : openedSelect === 'Вид блока', 'selected': checkedBlocks.length > 0}"
            >
            Вид блока
            <span class="delete" @click="deleteBlocks">&times;</span>
          </div>

          <div
            class="products__select"
            name="Формат"
            @click="openSelect"
            :class="{'open' : openedSelect === 'Формат'}"
            >
            Формат
          </div>

        </div>
        <ProductsNav :isOpen='openedSelect'/>
        <ProductFilter :product='product' :isOpen='openedSelect'/>

      </div>
    </div>
  </div>
</template>

<script>
import Breadcrumbs from '@/components/Breadcrumbs'
import ProductsNav from '@/components/ProductsNav'
import ProductFilter from '@/components/ProductFilter'

import { mapGetters } from 'vuex';

export default {

  components: {
    Breadcrumbs, ProductsNav, ProductFilter
  },
  data() {
    return {
      checkedModels: [],
      checkedBonds: [],
      checkedBlocks: [],
      queryModels: [],
      queryBonds: [],
      queryBlocks: [],
      productId: this.$route.params['id'],
      openedSelect: ''
    }
  },
  computed: {
    ...mapGetters([
      'getcheckedModels', 'getcheckedBonds', 'getcheckedBlocks',
      'getqueryModels', 'getqueryBonds', 'getqueryBlocks',
    ]),

    product() {
      const productId = this.$route.params['id']
      return this.$store.getters.getProductById(productId)
    }
  },
  mounted() {
    this.productId = this.$route.params['id']
    this.$store.commit('addProductId', this.productId)
  },
  methods: {
    deleteModels() {
      this.checkedModels = []
      this.queryModels = []
      this.$store.commit('checkedModels', this.checkedModels)
      this.$store.commit('queryModels', this.queryModels)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },
    deleteBonds() {
      this.checkedBonds = []
      this.queryBonds = []
      this.$store.commit('checkedBonds', this.checkedBonds)
      this.$store.commit('queryBonds', this.queryBonds)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },
    deleteBlocks() {
      this.checkedBlocks = []
      this.queryBlocks = []
      this.$store.commit('checkedBlocks', this.checkedBlocks)
      this.$store.commit('queryBlocks', this.queryBlocks)
      this.routerPush(this.queryModels, this.queryBonds, this.queryBlocks)
    },
    openSelect(event) {
      this.openedSelect = event.target.getAttribute('name')
    },
    routerPush(model, bond, block) {
      this.$router.push({
        query: {'model': model, 'bond': bond, 'block': block}
      })
    }
  },
  watch: {
    $route(toR) {
      this.productId = toR.params['id']
      this.$store.commit('addProductId', this.productId)
    },

    getcheckedModels() {
      this.checkedModels = this.getcheckedModels
    },
    getcheckedBonds() {
      this.checkedBonds = this.getcheckedBonds
    },
    getcheckedBlocks() {
      this.checkedBlocks = this.getcheckedBlocks
    },
    getqueryModels() {
      this.queryModels = this.getqueryModels
    },
    getqueryBonds() {
      this.queryBonds = this.getqueryBonds
    },
    getqueryBlocks() {
      this.queryBlocks = this.getqueryBlocks
    },
  }
}
</script>
