import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import Products from './data/products'
import Models from './data/models'
import Bonds from './data/bonds'
import Blocks from './data/blocks'
import Formats from './data/formats'

export default new Vuex.Store({
  state: {
    products: Products,
    productId: null,
    models: Models,
    bonds: Bonds,
    blocks: Blocks,
    formats: Formats,
    checkedModels: [],
    checkedBonds: [],
    checkedBlocks: [],
    queryModels: [],
    queryBonds: [],
    queryBlocks: [],
  },
  mutations: {
    addProductId(state, payload) {
      state.productId = payload
    },
    checkedModels(state, payload) {
      state.checkedModels = payload
    },
    checkedBonds(state, payload) {
      state.checkedBonds = payload
    },
    checkedBlocks(state, payload) {
      state.checkedBlocks = payload
    },
    queryModels(state, payload) {
      state.queryModels = payload
    },
    queryBonds(state, payload) {
      state.queryBonds = payload
    },
    queryBlocks(state, payload) {
      state.queryBlocks = payload
    },

  },
  actions: {


  },
  getters: {
    products: state => {
      return state.products
    },
    models: state => {
      return state.models
    },
    bonds: state => {
      return state.bonds
    },
    blocks: state => {
      return state.blocks
    },
    formats: state => {
      return state.formats
    },
    getcheckedModels: state => {
      return state.checkedModels
    },
    getcheckedBonds: state => {
      return state.checkedBonds
    },
    getcheckedBlocks: state => {
      return state.checkedBlocks
    },
    getqueryModels: state => {
      return state.queryModels
    },
    getqueryBonds: state => {
      return state.queryBonds
    },
    getqueryBlocks: state => {
      return state.queryBlocks
    },

    getProductById: state => id => {
      return state.products.find(product => product.alias === id)
    }


  }
})
