    <h3>Изделия</h3>
    <ul>
      <router-link
        v-for="(product, index) in products"
        :to="'/products/'+ product.alias"
        class="link"
        :key="index"
        tag="li"
      ><a>{{product.name}}</a></router-link>
    </ul>
  </div>
</template>

<script>
export default {
